import env from '../constants/env';

const API = () => {
	switch (env) {
		case 'local':
			return 'https://biz.safebuildingsapp.ca';
		case 'dev':
			return 'http://sb.mg2project.com';
		case 'staging':
			return 'http://sb.mg2project.com';
		default:
			return 'https://biz.safebuildingsapp.ca';
	}
};

export default API;
